@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html{
  font-family: 'Poppins', sans-serif;
}
body{
  min-height: 100vh;
  background: linear-gradient(to bottom, #63D8D1, #4E8DE3);
}
.todo{
  background-color: #fff;
  padding: 40px 25px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 5px 5px 15px rgba(0,0,0,0.2);
  max-width: 400px;
  transition: 2s linear;
  margin: 100px auto;
}
.todo-title{
  width: 100%;
  font-weight: 700;
}
.todo-form{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  margin-top: 40px;
  height: 40px;
  
}
.todo-form input{
  -webkit-flex: 1 1;
          flex: 1 1;
  border: 1px solid #ccc;
  border-radius: 2px;
  outline: none;
  padding: 0 12px;
}
.btn-add{
  background-color: #8E49E9;
  height: 100%;
  color: #fff;
  font-size: 22px;
  width: 40px;
  cursor: pointer;
  border-radius: 2px;
  margin-left: 5px;
}
.btn-add i{
  line-height: 40px;
}
.todo-list{
  padding-top: 20px;
 
}
.todo-item{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  background-color: rgb(235, 235, 235);
  border-radius: 2px;
  margin-bottom: 10px;
  padding: 10px 12px;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  overflow: hidden;
  
}
.todo-item h5{
  font-size: 16px;
  line-height: 1;
  margin: 0;
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: left;
  font-weight: 400;
  margin-left: 10px;
}
.todo-item h5.line{
  text-decoration: line-through;
}
.btn-del{
  position: absolute;
  height: 100%;
  background-color: #E84C3C;
  right: 0;
  width: 36px;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.warn-empty{
  color: #E84C3C;
  font-size: 13px;
  text-align: left;
  margin-top: 5px;
}
@media  (max-width: 768px) {
  .todo{
    margin: 100px 15px;
  }
}
